import { TransformToolComponent } from 'src/app/core/transform-tool/transform-tool.component';
import yaml from 'yaml';
import { NgModule } from '@angular/core';
import { CoreModule } from '../../core/core.module';
import { RouterModule } from '@angular/router';
import * as readme from './readme.md';

export class JsonToYamlComponent extends TransformToolComponent {
    id = 'json-to-yaml';
    action = 'Convert';
    schema = {
        type: 'object',
        properties: {
            input: { type: 'string', title: 'JSON', description: 'JSON to convert' }
        },
        required: ['input']
    };

    onTransform(transform: any): string {
        const result = yaml.parse(transform.input);
        return yaml.stringify(result);
    }

    documentation = readme.default;

}

@NgModule({
    declarations: [
        JsonToYamlComponent,
    ],
    imports: [
        CoreModule,
        RouterModule.forChild([
            { path: '', component: JsonToYamlComponent }
        ])
    ]
  })
export class JsonToYamlModule { }